@import url(https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap);
:root {
  --orange-120: #E87928;
  --orange-100: #FF8B37;
  --orange-80: #FFAC70;
  --orange-5: #FFF3EB;

  --green-100: #47D867;
  --green-5: #E6FAEA;

  --red-100: #EC6259;
  --red-5: #FDEEED;

  --black-100: #1C1C21;
  --black-40: #D0D0D8;
  --black-5: #F5F5F7;

  --white: #FFF;
}

:root {
  --font-xs: 14px;
  --font-xs-plus: 18px;
  --font-s: 20px;
  --font-m: 32px;
  --font-l: 56px;

  --lh-s: 16px;
  --lh-s-plus: 21px;
  --lh-m: 23px;
  --lh-l: 37px;
  --lh-xl: 65px;

  --border-radius: 12px;
}

*,
*:before,
*:after {
    box-sizing: border-box;
}

body {
    font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    font-weight: 500;
    font-size: var(--font-m);
    line-height: var(--lh-m);
    color: var(--black-100);
    margin: 0;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

html, body {
    height: 100%;
    margin: 0;
}

input, button, textarea, select {
    resize: none;
    border-radius: 0;
    font-family: inherit;
    line-height: var(--lh-m);
}

button {
    cursor: pointer;
}

h1, h2, h3, h4 {
    font-weight: 600;
}

a {
    transition: color .2s;
}

ul {
    padding: 0;
}

ul li {
    list-style: none;
}

p {
    margin: 0;
    padding: 0;
}

figure {
    margin: 0;
    padding: 0;
}

:root {
  --orange-120: #E87928;
  --orange-100: #FF8B37;
  --orange-80: #FFAC70;
  --orange-5: #FFF3EB;

  --green-100: #47D867;
  --green-5: #E6FAEA;

  --red-100: #EC6259;
  --red-5: #FDEEED;

  --black-100: #1C1C21;
  --black-40: #D0D0D8;
  --black-5: #F5F5F7;

  --white: #FFF;
}

:root {
  --font-xs: 14px;
  --font-xs-plus: 18px;
  --font-s: 20px;
  --font-m: 32px;
  --font-l: 56px;

  --lh-s: 16px;
  --lh-s-plus: 21px;
  --lh-m: 23px;
  --lh-l: 37px;
  --lh-xl: 65px;

  --border-radius: 12px;
}

.main {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
}

.main__wrapper {
  display: flex;
  align-items: center;
  padding-bottom: 3%;
}

.main__text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  max-width: 624px;
  margin-left: 32px;
}

.main__title {
  font-size: var(--font-l);
  line-height: var(--lh-xl);
  margin-top: 0;
  margin-bottom: 64px;
}

.main__image svg {
  width: 100%;
  height: 100%;
}

@media (max-width: 920px) {
  .main__wrapper {
    flex-direction: column;
  }

  .main__image {
    margin-top: 80px;
  }

  .main__wrapper {
    padding: 0 16px 48px;
    align-items: flex-end;
    justify-content: center;
  }

  .main__text {
    margin-left: 0;
    margin-top: 48px;
    align-self: flex-start;
    width: 100%;
    align-items: center;
    flex: 1 1;
  }

  .main__title {
    font-size: var(--font-m);
    line-height: var(--lh-l);
    text-align: center;
  }

  .main .btn {
    align-self: center;
    margin-top: auto;
  }
}

.main {
  background-image: linear-gradient(-38deg, var(--orange-5) 50%, var(--white) 50%);
}

@media (max-width: 1200px) {
  .main {
    background-image: linear-gradient(-37deg, var(--orange-5) 50%, var(--white) 50%);
  }
}

@media (max-width: 920px) {
  .main {
    background-image: linear-gradient(-41deg, var(--orange-5) 50%, var(--white) 50%);
  }
}

@media (max-width: 600px) {
  .main {
    background-image: linear-gradient(-53deg, var(--orange-5) 50%, var(--white) 50%);
  }
}

@media (max-width: 450px) {
  .main {
    background-image: linear-gradient(-61deg, var(--orange-5) 50%, var(--white) 50%);
  }
}

@media (max-width: 360px) {
  .main {
    background-image: linear-gradient(-66deg, var(--orange-5) 50%, var(--white) 50%);
  }
}

@media (max-width: 320px) {
  .main {
    background-image: linear-gradient(-68deg, var(--orange-5) 50%, var(--white) 50%);
  }
}

.btn {
  font-weight: 600;
  background-color: var(--orange-100);
  border-radius: var(--border-radius);
  color: var(--white);
  width: 296px;
  display: inline-flex;
  justify-content: center;
  cursor: pointer;
  transition: background-color 0.15s, color 0.15s;
  align-items: center;
  border: none;
}

.btn:hover {
  background-color: var(--orange-80);
}

.btn:active {
  background-color: var(--orange-120);
}

.btn:focus {
  outline: none;
  box-shadow: 4px 0 0 var(--orange-120),
  0 4px 0 var(--orange-120),
  -4px 0 0 var(--orange-120),
  0 -4px 0 var(--orange-120);
}

.btn--fullWidth {
  width: 100%;
}

.btn--s {
  font-size: var(--font-xs);
  line-height: var(--lh-s);
  padding: 14px 24px;
}

.btn--m {
  font-size: var(--font-s);
  line-height: var(--lh-m);
  padding: 20px 24px;
}

.MainLayout {
  grid-area: main;
  height: 100%;
  background-color: var(--black-5);
}

.MainLayout__answers-fieldset {
  margin: 0;
  padding: 0;
  border: 0;
  height: 100%;
  flex-wrap: wrap;
  display: flex;
  flex-direction: column;
}

.MainLayout__answers-form {
  display: flex;
  flex-direction: column;
  padding-left: 80px;
  padding-bottom: 122px;
  padding-top: 133px;
  height: 100%;
  background-color: transparent;
}

.MainLayout__answers-text {
  max-width: 624px;
  font-weight: 600;
  font-size: var(--font-m);
  line-height: var(--lh-l);
  padding: 0;
}

.MainLayout__answers-list {
  display: flex;
  flex-wrap: wrap;
  margin: auto 0 0;
  max-width: 870px;
}

.MainLayout__answers-item {
  width: 420px;
}

.MainLayout__answers-item:nth-child(n + 3) {
  margin-top: 32px;
}

.MainLayout .btn {
  margin-top: 32px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.MainLayout--loading {
  pointer-events: none;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

@media (max-width: 1400px) {
  .MainLayout__answers-item {
    width: auto;
  }
}

@media (max-width: 1350px) {
  .MainLayout__answers-item {
    width: 420px;
  }

  .MainLayout__answers-item:nth-child(n + 1) {
    margin-top: 32px;
  }
}

@media (max-width: 920px) {
  .MainLayout__answers-form {
    padding: 136px 0 40px 0;
  }

  .MainLayout__answers-fieldset {
    z-index: 2;
    display: flex;
    flex-direction: column;
  }

  .MainLayout__answers-text {
    text-align: center;
    padding: 0 16px;
    align-self: center;
  }

  .MainLayout__answers-item {
    flex-basis: 100%;
    width: 100%;
  }

  .MainLayout__answers-item + .MainLayout__answers-item {
    margin-top: 8px;
  }

  .MainLayout .btn {
    z-index: 2;
    align-self: center;
  }
}

@media (max-width: 600px) {
  .MainLayout .btn {
    width: calc(100% - 32px);
  }

  .MainLayout__answers-text {
    font-size: var(--font-s);
    line-height: var(--lh-s-plus);
  }
}

@media (max-width: 350px) {
  .MainLayout__answers-form {
    padding-top: 102px;
  }
}

.answer-cell {
  font-weight: normal;
  color: var(--black-100);
  width: 100%;
  border: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.answer-cell__line {
  margin: 0;
  position: absolute;
  top: 50%;
  width: 100%;
  height: 1px;
  border: 0;
  z-index: 0;
  right: 0;
  background-color: var(--black-40);
}

.answer-cell__input {
   width: 1px;
   height: 1px;
   position: absolute;
   opacity: 0;
}

.answer-cell__label {
  display: flex;
  align-items: center;
  cursor: pointer;
}

.answer-cell__text {
  position: absolute;
  z-index: 2;
  margin-left: 32px;
  max-width: 309px;
}

.answer-cell svg {
  z-index: 1;
}

.answer-cell__label:hover + .answer-cell__line {
  background-color: var(--orange-100);
}

.answer-cell__label:hover .answer-cell__icon {
  stroke: var(--orange-100);
}

.answer-cell__input:checked ~ .answer-cell__line {
  background-color: var(--orange-100);
}

.answer-cell__input:checked + .answer-cell__label  .answer-cell__icon {
  stroke: var(--orange-100);
  fill: var(--orange-5);
}

.answer-cell--wrong .answer-cell__line {
  background-color: var(--red-100) !important;
}

.answer-cell--wrong .answer-cell__icon {
  stroke: var(--red-100) !important;
  fill: var(--red-5) !important;
}

.answer-cell--correct .answer-cell__line {
  background-color: var(--green-100) !important;
}

.answer-cell--correct .answer-cell__icon {
  stroke: var(--green-100) !important;
  fill: var(--green-5) !important;
}

.answer-cell__variant {
  margin-right: 8px;
  color: var(--orange-100);
  font-weight: 600;
}

.answer-cell--s {
  font-size: var(--font-xs);
  line-height: var(--lh-s);
}

.answer-cell--m {
  font-size: var(--font-s);
  line-height: var(--lh-m);
}

.Header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  grid-area: header;
}

.Header__nav {
  margin: 0;
  width: 100%;
}

.Header__list {
  margin: 0;
  width: 100%;
}

.Header__item + .Header__item {
  margin-top: 8px;
}

@media (max-width: 920px) {
  .Header {
    background-color: var(--black-5);
    justify-content: flex-end;
    position: absolute;
    width: 100%;
    height: 100vh;
  }

  .Header--close + main {
    overflow: hidden;
  }

  .Header__menu {
    border: none;
    background: none;
    padding: 0;
    display: flex;
    position: absolute;
    top: 16px;
    right: 16px;
    z-index: 4;
  }

  .Header__nav {
    display: flex;
    align-items: center;
    height: 100%;
    z-index: 3;
    background-color: var(--black-5);
  }
}

.money-cell {
  font-weight: normal;
  color: var(--black-100);
  width: 100%;
  border: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
}

.money-cell__line {
  margin: 0;
  position: absolute;
  top: 50%;
  width: 100%;
  height: 1px;
  border: 0;
  z-index: 0;
  background-color: var(--black-40);
}

.money-cell svg {
  z-index: 1;
}

.money-cell__text {
  position: absolute;
  z-index: 2;
}

.money-cell--active {
color: var(--orange-100);
}

.money-cell--active .money-cell__line {
  background-color: var(--orange-100);
}

.money-cell--active .money-cell__icon {
stroke: var(--orange-100);
}

.money-cell--disabled {
  color: var(--black-40);
}

.money-cell--disabled .money-cell__line {
  background-color: var(--black-40);
}

.money-cell--disabled .money-cell__icon {
  stroke: var(--black-40);
}

.money-cell--s {
  font-size: var(--font-xs);
  line-height: var(--lh-s);
}

.money-cell--s svg {
  height: 32px;
}

.money-cell--m {
  font-size: var(--font-s);
  line-height: var(--lh-m);
}

.Footer {
  grid-area: footer;
  position: absolute;
  bottom: 12px;
  left: 12px;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
}

.Footer__text {
  color: var(--black-40);
  font-weight: 400;
  font-size: var(--font-xs);
  line-height: 1.4;
  padding: 0;
}

.main__title-total {
  opacity: .5;
  margin-top: 0;
  margin-bottom: 8px;
  font-size: var(--font-m);
  line-height: var(--font-l);
}

@media (max-width: 920px) {
  .main__title-total {
    font-size: var(--font-xs-plus);
    line-height: var(--lh-s-plus);
  }
}

.App {
  display: grid;
  grid-template-areas: 'main header' 'footer header';
  grid-template-rows:  1fr -webkit-max-content;
  grid-template-rows:  1fr max-content;
  grid-template-columns: 2fr 1fr;
  height: 100vh;
  position: relative;
}

@media (max-width: 920px) {
  .App {
    grid-template-areas: 'header' 'main' 'footer';
    grid-template-columns: 1fr;
  }
}

